.overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 1;
  }

.cart {
  font-size: calc(18 / 1440 * 100vw);
    display: flex;
    flex-direction: column;
    position: absolute;
    width: calc(500 / 1440 * 100vw);
    height: 100%;
    right: 0;
    background-color: #ffffff;
    box-shadow: calc(1 / 1440 * 100vw) calc(4 / 1440 * 100vw) calc(24 / 1440 * 100vw) rgba(0, 0, 0, 0.1);
    padding: calc(30 / 1440 * 100vw);
    h2 {
      margin-bottom: calc(20 / 1440 * 100vw);
    }
  }
  
.cartClose {
  position: absolute;
  top: calc(20 / 1440 * 100vw);
  right: calc(20 / 1440 * 100vw);
  width: calc(27 / 1440 * 100vw);
  cursor: pointer;
  opacity: 0.5;
  transition: opacity .25s;
}

.cartClose:hover {
  opacity: 1;
}

  .cartItems {
    flex: 1;
    overflow: auto;
    margin-bottom: calc(20 / 1440 * 100vw);
  }
  
  .cartItem {
    display: flex;
    align-items: center;
    font-size: calc(14 / 1440 * 100vw);
    border: 1px solid #f3f3f3;
    border-radius: calc(20 / 1440 * 100vw);
    margin-bottom: calc(20 / 1440 * 100vw);
    b {
      font-size: calc(18 / 1440 * 100vw);
    }
    div {
      margin-right: calc(20 / 1440 * 100vw);
    }
  }
  
  .cartItemImage {
    width: calc(75 / 1440 * 100vw);
    margin: 0 calc(25 / 1440 * 100vw);
  }
  
  .cartItemRemove {
    width: calc(27 / 1440 * 100vw);
    opacity: 0.25;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;
  }
  
  .cartItemRemove:hover {
    opacity: 1;
  }
  
  .cartOverall {
    button {
    width: 100%;
    height: calc(55 / 1440 * 100vw);
    margin-top: calc(20 / 1440 * 100vw);
    background: #7e7e7e;
    border-radius: calc(18 / 1440 * 100vw);
    border: 0;
    color: #ffffff;
    font-size: calc(20 / 1440 * 100vw);
    font-weight: 700;
    }
    button:hover {
      background-color: lighten($color: #9dd558, $amount: 10%) ;
    }
    button:active {
      background-color: darken($color: #9dd558, $amount: 10%) ;
    }
  }
  
  .cartOverallPrice {
    display: flex;
    justify-content: space-between;
  }