.card {
    border: 1px solid #f8f8f8;
    padding: calc(20 / 1440 * 100vw);
    margin: calc(30 / 1440 * 100vw);
    width: calc(222 / 1440 * 100vw);
    height: calc(450 / 1440 * 100vw);
    border-radius: calc(40 / 1440 * 100vw);
    box-shadow: calc(5 / 1440 * 100vw) calc(5 / 1440 * 100vw) calc(20 / 1440 * 100vw) rgb(215, 215, 215);

    h2 {
        font-weight: 700;
        font-size: calc(14 / 1440 * 100vw);
    }
}

.cardImage {
    display: block;
    object-fit: contain;
    max-width: calc(182 / 1440 * 100vw);
    max-height: calc(200 / 1440 * 100vw);
    margin: 0 auto calc(20 / 1440 * 100vw);
}

.cardDescription {
    font-size: calc(12 / 1440 * 100vw);
    height: calc(60 / 1440 * 100vw);
    overflow: hidden;
    margin-bottom: calc(20 / 1440 * 100vw);
}

.cardBottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
        display: flex;
        flex-direction: column;
    }

    span {
        font-size: calc(14 / 1440 * 100vw);
        text-transform: uppercase;
        opacity: 0.5;
    }

    b {
        font-size: calc(18 / 1440 * 100vw);
    }
}

.cardAddToCart {
    border: 1px solid #f2f2f2;
    box-sizing: border-box;
    border-radius: calc(8 / 1440 * 100vw);
    width: calc(50 / 1440 * 100vw);
    height: calc(50 / 1440 * 100vw);
    cursor: pointer;
}